@mixin btn-style {
    padding: 0.9375rem 0 0.875rem;
    vertical-align: middle;
    align-items: center;
    border-radius: 29px;
    border: transparent;
    margin: 0 0 1rem;
    display: flex;
    justify-content: center;
    font-size: 16px;
    line-height: 26px;
    font-weight: 900;
    letter-spacing: -0.18px;
  }